[class*=mif-] {
    font-family: 'metro'!important;
    speak: none;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;
    display: inline-block;
    position: relative;
}

.mif-lg {
    font-size: 1.2em;
}

.mif-2x {
    font-size: 1.5em;
}
.mif-3x {
    font-size: 2em;
}
.mif-4x {
    font-size: 2.5em;
}
.mif-5x {
    font-size: 3em;
}
.mif-6x {
    font-size: 3.5em;
}
.mif-7x {
    font-size: 4em;
}
.mif-8x {
    font-size: 4.5em;
}
.mif-9x {
    font-size: 5em;
}
.mif-10x {
    font-size: 5.5em;
}
